module.exports = ":- use_module(library(lists)).\r\n:- use_module(library(random)).\r\n\r\n%Params: Path, Errors, Map, Result\r\ngetMove(P, _, M, X, O) :-\r\n    O >= 0,\r\n    member({P,X}, M).\r\n\r\ngetMove(_, E, _, X, O) :-\r\n    O >= 0,\r\n    subtract([(1,1), (2,1), (3,1), (1,2), (2,2), (3,2), (1,3), (2,3), (3,3)], E, PL),\r\n    random_member(X, PL).\r\n\r\n%Auxillary Function\r\nsubtract([], _, []) :- !.\r\nsubtract([A|C], B, D) :-\r\n        member(A, B), !,\r\n        subtract(C, B, D).\r\nsubtract([A|B], C, [A|D]) :-\r\n        subtract(B, C, D)."